  .metric-count {
    background: $plasma-blue;
    color: white;
    padding: 2px 6px;
    border-radius: 3px;
    font-size: 11px;
    font-weight: 600;
    min-width: 16px;
    text-align: center;
  }
}

.panel-content {
  flex: 1;
  padding: 12px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  
  img {
    max-width: 100%;
    height: auto;
    border-radius: 4px;
    background: transparent;
  }
  
  .metrics-overview {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .metrics-chart {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .activity-graph {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .achievements-grid {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .performance-metrics {
    display: flex;
    justify-content: center;
    align-items: center;
