}

.process-list {
  flex: 1;
  overflow-y: auto;
  
  .loading {
    display: flex;
    align-items: center;
    justify-content: center;
    color: $plasma-blue;
    font-size: 13px;
    gap: 8px;
    height: 100px;
  }
  
  .repo-item, .org-item {
    background: $plasma-window-bg;
    border: 1px solid rgba(77, 81, 87, 0.3);
    border-radius: 4px;
    padding: 8px 12px;
    margin-bottom: 4px;
    transition: all 0.2s;
    font-size: 12px;
    
    &:hover {
      background: $plasma-hover;
      border-color: $plasma-blue;
    }
    
    .repo-name, .org-name {
      color: $plasma-blue;
      font-weight: 600;
      margin-bottom: 4px;
      text-decoration: none;
      display: block;
      
      &:hover {
