    .repo-description, .org-description {
      color: $plasma-text;
      font-size: 11px;
      margin-bottom: 4px;
      line-height: 1.3;
    }
    
    .repo-meta, .org-meta {
      display: flex;
      gap: 8px;
      font-size: 10px;
      color: $plasma-text-disabled;
      
