      }
    }
  }
  
  .file-list {
    flex: 1;
    overflow-y: auto;
    
    &::-webkit-scrollbar {
      width: 8px;
    }
    
    &::-webkit-scrollbar-track {
      background: $plasma-panel-bg;
    }
    
    &::-webkit-scrollbar-thumb {
      background: $plasma-border;
      border-radius: 4px;
      
      &:hover {
        background: lighten($plasma-border, 20%);
      }
    }
    
    .loading-files {
      display: flex;
      align-items: center;
      justify-content: center;
      color: $plasma-blue;
      font-size: 14px;
      gap: 8px;
      height: 200px;
    }
    
    .file-entry {
      display: flex;
      align-items: center;
      padding: 8px 0;
      border-bottom: 1px solid rgba(77, 81, 87, 0.1);
      cursor: pointer;
      transition: all 0.2s;
      
      &:hover {
        background: $plasma-hover;
      }
      
      &.selected {
        background: $plasma-active;
      }
      
      .file-name {
        flex: 2;
        min-width: 200px;
        padding: 0 12px;
        display: flex;
        align-items: center;
        gap: 8px;
        
        i {
          color: $plasma-blue;
          font-size: 16px;
          width: 20px;
        }
        
        .name-text {
          color: $plasma-text;
          font-size: 13px;
          font-weight: 500;
        }
      }
      
      .file-size {
        flex: 0 0 80px;
        padding: 0 12px;
        color: $plasma-text-disabled;
        font-size: 12px;
        text-align: right;
      }
      
      .file-type {
        flex: 0 0 100px;
        padding: 0 12px;
        color: $plasma-text-disabled;
        font-size: 12px;
      }
      
      .file-date {
        flex: 0 0 120px;
        padding: 0 12px;
        color: $plasma-text-disabled;
        font-size: 12px;
      }
    }
  }
}

.ark-statusbar {
  background: $plasma-panel-bg;
  border-top: 1px solid $plasma-border;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;
  font-size: 12px;
  color: $plasma-text-disabled;
  flex-shrink: 0;
  
  .status-section {
    display: flex;
    align-items: center;
    gap: 16px;
  }
}

.archive-post {
  margin-left: 40px;
  border-left: 2px solid $plasma-border;
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    left: -6px;
    top: 50%;
    width: 10px;
    height: 10px;
    background: $plasma-blue;
    border-radius: 50%;
    transform: translateY(-50%);
  }
}

// Responsive Design
@media (max-width: 1200px) {
  .desktop-content {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;
    margin: 15px;
    height: calc(100vh - 120px);
  }
  
  .fm-sidebar {
    width: 220px;
  }
  
  .terminal-window {
    top: 3%;
    left: 3%;
    width: 94%;
    height: 94%;
  }
  
  .file-item {
    .file-details {
      .file-label {
        font-size: 13px;
      }
    }
  }
}

@media (max-width: 1024px) {
  .desktop-content {
    margin: 12px;
    height: calc(100vh - 100px);
  }
  
  .fm-sidebar {
    width: 180px;
  }
  
  .terminal-window {
    top: 2%;
    left: 2%;
    width: 96%;
    height: 96%;
  }
  
  .folder-view.grid-view {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: 15px;
  }
  
  .file-item .file-icon {
    font-size: 32px;
  }
  
  .plasma-taskbar {
    .app-menu-button {
      padding: 8px 12px;
      font-size: 13px;
    }
    
    .taskbar-search {
      input {
        font-size: 13px;
      }
    }
  }
}

@media (max-width: 768px) {
  .plasma-desktop {
    overflow-x: hidden;
  }
  
  .desktop-content {
    margin: 8px;
    height: calc(100vh - 80px);
  }
  
  .fm-sidebar {
    position: fixed;
    top: 0;
    left: -250px;
    width: 250px;
    height: 100vh;
    background: var(--plasma-window-bg);
    border-right: 1px solid var(--plasma-border);
    z-index: 1000;
    transition: left 0.3s ease;
    
    &.mobile-open {
      left: 0;
    }
  }
  
  .file-browser {
    padding: 8px;
    padding-top: 60px; // Account for mobile header
  }
  
  .folder-view.grid-view {
    grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
    gap: 8px;
  }
  
  .file-item {
    .file-icon {
      font-size: 24px;
    }
    
    .file-details {
      .file-label {
        font-size: 11px;
        line-height: 1.3;
      }
      
      .file-description {
        font-size: 9px;
        line-height: 1.2;
      }
    }
    
    // Increase touch targets
    min-height: 44px;
    cursor: pointer;
    
    &:hover {
      transform: none; // Remove hover animations on mobile
    }
  }
  
  .terminal-window {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 0;
    border: none;
  }
  
  .plasma-taskbar {
    height: 44px; // Larger touch target
    padding: 0 8px;
    
    .app-menu-button {
      padding: 10px 15px;
      font-size: 14px;
      min-width: 44px;
    }
    
    .taskbar-search {
      display: none; // Hide search on mobile
    }
    
    .system-tray {
      gap: 8px;
      
      .tray-icon {
        width: 36px;
        height: 36px;
        font-size: 14px;
        min-width: 44px;
        min-height: 44px;
      }
    }
    
    .taskbar-clock {
      .clock-time {
        font-size: 14px;
      }
      
      .clock-date {
        font-size: 11px;
      }
    }
  }
  
  // Mobile navigation overlay
  .mobile-nav-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
    display: none;
    
    &.active {
      display: block;
    }
  }
  
  // Mobile header for navigation
  .mobile-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    background: var(--plasma-panel-bg);
    border-bottom: 1px solid var(--plasma-border);
    z-index: 1001;
    display: flex;
    align-items: center;
    padding: 0 15px;
    
    .mobile-menu-button {
      width: 44px;
      height: 44px;
      background: none;
      border: none;
      color: var(--plasma-text);
      font-size: 18px;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 4px;
      
      &:hover {
        background: var(--plasma-hover-bg);
      }
    }
    
    .mobile-title {
      margin-left: 15px;
      font-size: 16px;
      font-weight: 500;
      color: var(--plasma-text);
    }
  }
}

@media (max-width: 480px) {
  .desktop-content {
    margin: 4px;
    height: calc(100vh - 70px);
  }
  
  .file-browser {
    padding: 4px;
    padding-top: 54px;
  }
  
  .folder-view.grid-view {
    grid-template-columns: repeat(auto-fill, minmax(65px, 1fr));
    gap: 4px;
  }
  
  .file-item {
    .file-icon {
      font-size: 20px;
    }
    
    .file-details {
      .file-label {
        font-size: 10px;
        line-height: 1.2;
      }
      
      .file-description {
        font-size: 8px;
        display: none; // Hide descriptions on very small screens
      }
    }
  }
  
  .plasma-taskbar {
    height: 40px;
    padding: 0 4px;
    
    .app-menu-button {
      padding: 8px 10px;
      font-size: 12px;
    }
    
    .system-tray {
      gap: 4px;
      
      .tray-icon {
        width: 32px;
        height: 32px;
        font-size: 12px;
      }
    }
    
    .taskbar-clock {
      .clock-time {
        font-size: 12px;
      }
      
      .clock-date {
        font-size: 9px;
      }
    }
  }
  
  .mobile-header {
    height: 44px;
    padding: 0 10px;
    
    .mobile-title {
      font-size: 14px;
    }
  }
}
